
import Scroller from 'vux/src/components/scroller/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

/**/
import WPolicy from './item_temp/Policy';
import WVisit from './item_temp/Visit';
import WGoods from './item_temp/Goods';
import WSendWork from './item_temp/SendWork';
export default {
components: {
Scroller,
Previewer,
Tab,
TabItem,
WPolicy,
WVisit,
WGoods,
WSendWork
},
beforeCreate() {


this._id = this.querystring.parse().family_id;
this.W.AJAX(this.$http, {
url: this.W.API.INDEX + this._id + '/',
method: 'get',
suc: res => {
this.U = res;
document.title = res.family.name;
let mobile = navigator.userAgent.toLowerCase()
if (/iphone|ipad|ipod/.test(mobile)) {
let iframe = document.createElement('iframe')
iframe.style.visibility = 'hidden'


let iframeCallback = function () {
setTimeout(function () {
iframe.removeEventListener('load', iframeCallback)
document.body.removeChild(iframe)
}, 0)
}
iframe.addEventListener('load', iframeCallback)
document.body.appendChild(iframe)
}
this.$refs.scroller.disablePullup();

this.$nextTick(() => this.$refs.scroller.reset());
},
err: err => {
this.$refs.scroller.disablePullup();
}
});


this.APIs = [
'',
this.W.API.VISIT,
this.W.API.GOODS,
this.W.API.SENDWORK
];
},
data() {
return {
tab: 0,

U: {
family: {
counts: {},
earning: {},
causes:[]
},
helper: {
gov: {}
},
solutions: {
production: {
images: []
},
relocate: {
images: []
}
}
},
datas: {
V: {
has_more: true,
list: []
},
G: {
has_more: true,
list: []
},
S: {
has_more: true,
list: []
},
},
isHide: false,
};
},

computed: {
User() {
let U = this.U;
let isHideStatus = false;
U.family.earning = U.family.earning || {};
U.family.helper = U.family.helper || {};
U.family.phone = this.W.ReplaceCode(U.family.phone, 4, 4);
U.family.causes = this.W.GetCau(U.family.causes);
U.family.property = this.W.GetPro(U.family.property);
U.family.avatar = this.W.GetAvatar(U.family.avatar);
U.helper = U.helper || {
gov: {}
};
U.helper.avatar = this.W.GetAvatar(U.helper.avatar);

let keysArr = ['compensate', 'education', 'guarantee', 'health', 'production', 'relocate'];
for (let val of keysArr) {
if (U.solutions[val] && U.solutions[val].joined) {
isHideStatus = true;
break;
};
};
if (isHideStatus) {

for (let key in U.solutions) {
if (key != '_id') {
U.solutions[key] = U.solutions[key] || {
images: []
};
};
};
this.isHide = false;
} else {
this.isHide = true;
};
return U;
}
},
methods: {
scrollerUp() {

LoadList.call(this, this.tab);
},

tabItem(i) {

this.tab = i;

this.$nextTick(() => this.$refs.scroller.reset());

let keys = ['', 'V', 'G', 'S'];
let key = keys[i];
if (key) {
let targetJson = this.datas[key];
if (targetJson.has_more) {
this.$refs.scroller.enablePullup();
} else {
this.$refs.scroller.disablePullup();
};

if (!targetJson.list.length) {

LoadList.call(this, i);
};
};
}
}
};

/**/
function LoadList(i, scrollUp, cb) {
if (!i) return;

let API = this.APIs[i];

let key = '';
let resKey = '';
switch (i) {
case 1:
key = 'V';
resKey = 'visits';
API += this._id + '/visits/';
API += '?next_start=' + (this.datas[key].next_start || '');
break;
case 2:
key = 'G';
resKey = 'goods';
API += `?family_id=${this._id}`;
API += '&next_start=' + (this.datas[key].next_start || '');
break;
case 3:
key = 'S';
resKey = 'labors';
API += `?family_id=${this._id}`;
API += '&next_start=' + (this.datas[key].next_start || '');
break;
};


this.W.AJAX(this.$http, {
url: API,
method: 'get',
suc: res => {
this.datas[key].has_more = res.has_more;
this.datas[key].next_start = res.next_start;
this.datas[key].list = [...this.datas[key].list, ...res[resKey]];
if (res.has_more) {
this.$refs.scroller.donePullup();
} else {
this.$refs.scroller.disablePullup();
};
this.$nextTick(() => this.$refs.scroller.reset());
cb && cb();
}
});
};



