
import WBanner from './item_temp/Banner';
import dateFormat from 'vux/src/tools/date/format.js'

export default {
name: 'GoodsDetail',
components: {
WBanner
},
filters: {
delivery(val) {
let str = '';
let timeAt = 'YYYY-MM-DD';
if (val.ready == 1) {
str = '';
} else {
str = dateFormat(val.start * 1000, timeAt) +
'' + dateFormat(val.end * 1000, timeAt);
};
return str;
}
},
beforeCreate() {
let _id = this.$route.params._id;
let r_id = this.$route.params.r_id;
let API = this.W.API.GOODSDETAIL + _id + '/detail/';
this.W.AJAX(this.$http, {
url: API,
headers: {
x_region_id: r_id
},
suc: res => {
res.goods.cover = this.W.GetAvatar(res.goods.cover);
res.goods.family.cause = this.W.GetCau(res.goods.family.cause);
res.goods.family.property = this.W.GetPro(res.goods.family.property);
this.G = res.goods;
},
err: err => {
if (err.data) {
this.$vux.toast.text(err.data.result.msg, 'bottom');
setTimeout(() => {
window.history.back();
}, 300);
};
}
});
},
data() {
return {
G: {
delivery: {},
family: {
avg_income: {}
}
}
};
},
methods: {
GetSrc(src) {
return this.W.GetAvatar(src);
}
},
};



