import dateFormat from 'vux/src/tools/date/format.js'

class W {

constructor(Vue) {

this.Vue = Vue;
this.pullUpConfig = {
'content': '',
'defaultContent': '',
'pullUpHeight': 40,
'height': 30,
'autoRefresh': false,
'downContent': '<span class=wang-upDownIcon></span>',
'upContent': '<span class=wang-upDownIcon></span>',
'loadingContent': '<span class=w-default-color>..</span>',
'clsPrefix': 'xs-plugin-pullup-'
};


this.IMGHOST = 'http://img.cdn.gzhxyc.com/';
this.HOST = '';

this.API = {
INDEX: this.HOST + '/api/family/family_info/',
VISIT: this.HOST + '/api/family/',
GOODS: this.HOST + '/api/family/goods_list/',
SENDWORK: this.HOST + '/api/family/labors_list/',


GOODSDETAIL: this.HOST + '/api/mall/',
WORKDETAIL: this.HOST + '/api/jobs/labors/'
};
};
/**
* 
* 
*
* @param {any} vm Vue
* @param {any} index 
* @returns
* @memberof W
*/
ReturnNavsIndex(vm, index) {
let nullArr = [0, 0, 0, 0, 0];
nullArr[index] = 1;
return nullArr;
};
/**
* 
*
* @param {any} targetname  li
* @param {any} target     
* @returns
* @memberof W
*/
GetParent(targetname, target) {
targetname = targetname.toLocaleUpperCase();
let tgname = target.tagName;
if (targetname === tgname) {
return target;
} else if (tgname === 'BODY') {
return;
} else {
return this.GetParent(targetname, target.parentNode);
};
};
/**
* class
*
* @param {any} targetname
* @param {any} target
* @returns
* @memberof W
*/
GetClassParent(className, target) {
let cName = target.classList.contains(className);
if (cName) {
return target;
} else if (target.tagName === 'BODY') {
return;
} else {
return this.GetClassParent(className, target.parentNode);
};
};
/**
* VUX $HTTPAJAX
* jq.ajax
* @memberof W
*/
AJAX($http, option) {
if (!arguments[0] || !arguments[1]) return;

option.timeout = option.timeout || 60000;
$http(option)
.then(res => {

if (res.data.code) {
option.err && option.err(res);
return;
};
option.suc && option.suc(res.data.result);
}, res => {

option.err && option.err(res);
});
};
/**
* *
* @param {any} firstLength 
* @param {any} lastLength  
* @param {any} strLength   
* @memberof W
*/
ReplaceCode(value, firstLength, lastLength, strLength = 4) {
firstLength = parseInt(firstLength);
lastLength = parseInt(lastLength);
firstLength = isNaN(firstLength) ? 0 : firstLength;
lastLength = isNaN(lastLength) ? 0 : lastLength;
let argmentsLength = parseInt(firstLength) + parseInt(lastLength);
let str = '';
for (let i = 0; i < strLength; i++) { str += '*' };

if (!value || value.length < argmentsLength) {

return value;
};

let start = value.substr(0, firstLength);
let end = value.substr(-lastLength, lastLength);
return start + str + end;
};
/**
* 
*
* @param {any} code
* @memberof W
*/
GetCau(code) {
console.log(code);
let arr = ['', '', '', '','', '', '', '', '', '', '', ''];
let str = '';
code.forEach(item => {
str += arr[item]+' ';
});
return str || '';
};
/**
* 
*
* @param {any} code
* @memberof W
*/
GetPro(code) {
let arr = ['', '', '', '', '', '', ''];
return arr[code] || '';
};
/**
* 
*
* @param {any} code
* @memberof W
*/
GetEdu(code) {
let arr = ['', '', '', '', '', '', ''];
return arr[code] || '';
};
/**
* 
*
* @param {any} code
* @returns
* @memberof W
*/
GetTags(code) {
let arr = ['', '', '', ''];
return arr[code] || '';
};
/**
* 
*
* @param {any} avatar
* @returns
* @memberof W
*/
GetAvatar(avatar) {
let src = '';
if (avatar) {
src = this.IMGHOST + avatar;
} else {
src = null;
};
return src;
};
/**
* 
*
* @param {any} value
* @param {any} str
* @returns
* @memberof W
*/
DateFormat(value, str) {
return dateFormat(value, str);
};
/**
* 
*
* @param {any} code
* @memberof W
*/
GetDif(code) {
let arr = ['', '', '', ''];
return arr[code] || '';
};
};



export default { install(Vue) { Vue.prototype.W = new W(Vue) } }


