
import dateFormat from 'vux/src/tools/date/format.js'


export default {
name: 'Policy',
props: ['data', 'members', 'isHide'],
filters: {
D(value) {
return dateFormat(value * 1000, 'YYYY-MM-DD');
}
},
computed: {
_difficulty() {
this.data.relocate.difficulty = this.data.relocate.difficulty || -1;
return this.W.GetDif(this.data.relocate.difficulty);
console.log(this.data.relocate.difficulty);
}
},
data() {
return {};
},
methods:{
GetSrc(src) {
return this.W.GetAvatar(src);
},
GCons(level) {
let arr= ['','','','',''];
return arr[level] || '';
},
}
};



