
import dateFormat from 'vux/src/tools/date/format.js'


export default {
name: 'SendWork',
props: ['data'],
computed: {
list() {
return this.data.list;
}
},
filters: {
D(value) {
return dateFormat(value * 1000, 'YYYY-MM-DD');
}
},
data() {
return {};
},
methods: {
GetSrc(src) {
return this.W.GetAvatar(src);
},
GetTags(code) {
return this.W.GetTags(code);
}
}
};



