import Vue from 'vue'
import Router from 'vue-router'
/**
 * 
 */
import Index from '@/components/Index';
import Goods from '@/components/GoodsDetail';
import Work from '@/components/WorkDetail';

Vue.use(Router);

export default new Router({
    mode: 'history',
    // mode: 'hash',
    //
    base: __dirname,
    routes: [{
        path: '/poor.html',
        name: 'Index',
        component: Index
    }, {
        path: '/goods',
        name: 'Goods',
        component: Goods
    }, {
        path: '/work',
        name: 'Work',
        component: Work
    }]
})


