import WBanner from './item_temp/Banner';
export default {
    name: 'GoodsDetail',
    components: {
        WBanner
    },
    beforeCreate() {
        //WORKDETAIL
        //let _id = '59ba63774cedfd0001529d17'; //this.$route.params._id;1
        let _id = this.$route.params._id;
        let API = this.W.API.WORKDETAIL + _id + '/';
        this.W.AJAX(this.$http, {
            url: API,
            suc: res => {
                res.labor.photo = this.W.GetAvatar(res.labor.photo);
                res.labor.family.cause = this.W.GetCau(res.labor.family.cause);
                res.labor.family.property = this.W.GetPro(res.labor.family.property);
                this.Work = res.labor;
            },
            err: err => {
                if (err.data) {
                    this.$vux.toast.text(err.data.result.msg, 'bottom');
                    setTimeout(() => {
                        window.history.back();
                    }, 300);
                };
            }
        });
    },
    data() {
        return {
            Work: {
                family: {
                    avg_income: {}
                }
            }
        };
    },
    methods: {
        GetSrc(src) {
            return this.W.GetAvatar(src);
        },
        GetEdu(code) {
            return this.W.GetEdu(code);
        }
    },
};



